import {
  AuthenticationProvider,
  CookieProvider,
  DeviceInfoProvider,
  EnvironmentProvider
} from "@29cm/contexts";
import { FeatureFlagProvider } from "@29cm/feature-flag";
import { createQueryClient, QueryClientProvider } from "@29cm/react-query";
import {
  BottomSheetContainer,
  DialogContainer,
  DraggableBottomSheetContainer,
  UioThemeProvider
} from "@29cm/uio";
import { Global } from "@emotion/react";
import { initialize, mswDecorator } from "msw-storybook-addon";
import * as nextImage from "next/image";
import React, { useRef } from "react";
import { env } from "../config/env";
import { globalStyle } from "../src/styles/globalStyle";
import { theme } from "../src/styles/theme";

// Initialize MSW
initialize();

Object.defineProperty(nextImage, "default", {
  configurable: true,
  value: props => <img {...props} />
});

export const parameters = {
  layout: "fullscreen",
  actions: { argTypesRegex: "^on[A-Z].*" },
  controls: {
    matchers: {
      color: /(background|color)$/i,
      date: /Date$/
    }
  }
};

const UNLEASH_CONFIG = {
  url: env.sdk.unleash.proxy,
  clientKey: env.sdk.unleash.clientSecret,
  appName: `front-commerce-product`,
  environment: env.name,
  refreshInterval: 60
};

const withApp = Story => {
  const queryClientRef = useRef(createQueryClient());

  return (
    <>
      <link
        rel="stylesheet"
        href="https://d13fzx7h5ezopb.cloudfront.net/fonts/font.css"
      />
      <EnvironmentProvider environment={env} workspace="product">
        <DeviceInfoProvider>
          <UioThemeProvider theme={theme}>
            <QueryClientProvider client={queryClientRef.current}>
              <Global styles={globalStyle} />
              <CookieProvider>
                <AuthenticationProvider>
                  <FeatureFlagProvider config={UNLEASH_CONFIG}>
                    <DialogContainer />
                    <BottomSheetContainer />
                    <DraggableBottomSheetContainer />
                    <Story />
                  </FeatureFlagProvider>
                </AuthenticationProvider>
              </CookieProvider>
            </QueryClientProvider>
          </UioThemeProvider>
        </DeviceInfoProvider>
      </EnvironmentProvider>
    </>
  );
};

export const decorators = [withApp];
